.story{
    width: 100%;
    // height: auto;
    .hero_block{
        width: 100%;
        height: 100vh;
        position: relative;

        .hero_img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            position: absolute;
        }
        .hero_text{
            position: absolute;
            padding-left: 30px;
            color: #fff;

            &_top{
                top: 0;
                // padding-top: 50px;
            }
            &_bttm{
                bottom: 0
            }

        }
        .hero_text_top{
            position: absolute;
            padding-left: 30px;
            color: #fff;
            position: absolute;
            top: 0;
        }
        .hero_text_bttm{
            position: absolute;
            padding-left: 30px;
            color: #fff;
            position: absolute;
            bottom: 0;
        }
    }

    .block{
        width: 70%;
        height: 100%;
        margin: 0 auto;
        display: flex;
        padding-top: 150px;
        // display: grid;
        // grid-template-columns: repeat(2, 1fr);
        flex-direction: row;
        justify-content: space-around;
        // grid-auto-flow: column;

        &_left{
            width: 40%;
            order: 1;
            text-align: justify;
        }

        &_right{
            width: 40%;
            order: 2;
            text-align: justify;
        }
        &_single{
            margin: 0 auto;
            width: 50%;
        }
    }
}