.products{
    width: 80%;
    padding-top: 50px;
    margin: auto;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(4, 1fr);
    text-align: center;
    .prodLink{
        text-decoration: none;
        color: #000;
    }
    .prodTitle{
        color: #30aedf;
    }
    .product-info{
        border: 1px solid #000;
        border-radius: 10px;
        cursor: pointer;
    }
}