.container{
    width: 100%;
    min-height: 40vh ;
    display: flex;
    .product{
        position: relative;
        max-width: 400px;
        display: flex;
        flex-direction: column;
        width: fit-content;
        margin: auto;
        border-radius: 15px;
        padding: 0 20px;
        background-color: rgba(206, 227, 235, 0.385);
        .title{
            color: #30aedf;
        }
        .quantity{
            padding-top: 10px;

            button{ 
                border: none;
                background-color: #30aedf;
                color: #fff;
                margin: 0 5px;
                padding: 5px 10px;
                border-radius: 5px;
            }
        }
        .img, .quantity{
            margin: 0 auto;
        }
        .button{
            position: absolute;
            margin: 0 15px 15px 0px;
            padding: 5px 10px;
            border: none;
            border-radius: 5px;
            background-color: #30aedf;
            color: #fff;
            bottom: 0;
            right: 0;
            cursor: pointer;
        }
    }

}